.todos{--height-item: 72px;--indent-item: 12px;--indent-text: 20px;--size-button: 36px;--shadow: 0px 3px 7px 0px rgb(70 75 79 / 20%);--size-pseudo: 8px;position:relative;z-index:1;max-width:600px;width:100%;background:#fff;box-shadow:0px 3px 7px 0px rgba(70,75,79,.2)}.todos:before,.todos:after{content:"";position:absolute;left:50%;transform:translateX(-50%);height:var(--size-pseudo);background:#f6f6f6;border-bottom:1px solid #d2d2d2}.todos:before{bottom:calc(var(--size-pseudo)*-1);z-index:-1;width:99.2%;box-shadow:0px 1px 7px 0px rgba(70,75,79,.2)}.todos:after{z-index:-2;bottom:calc(var(--size-pseudo)*-2);width:98%;box-shadow:0px 2px 7px 0px rgba(70,75,79,.2)}.todos__title{margin-bottom:20px;font-size:80px;line-height:1;color:var(--color-2);text-align:center}.todos__form{display:flex;align-items:center;min-height:var(--height-item);padding:var(--indent-item);border-bottom:3px solid var(--color-1)}.todos__button-add{position:relative;flex-shrink:0;width:var(--size-button);height:var(--size-button);font-size:0}.todos__button-add:before{content:"";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%) rotate(45deg);width:12px;height:12px;border:3px solid var(--color-2);border-left:0;border-top:0}.todos__input{width:100%;height:100%;padding:0 var(--indent-text);font-size:var(--text-1);line-height:var(--line-height-1)}.todos__input::placeholder{font-size:var(--text-1);line-height:var(--line-height-1);color:var(--color-2);font-style:italic}.todos__list{list-style-type:none}.todos__item{display:flex;align-items:center;min-height:var(--height-item);padding:var(--indent-item);border-bottom:2px solid var(--color-1)}.todos__item_completed .todos__check{border-color:var(--color-5)}.todos__item_completed .todos__check:before{opacity:1}.todos__item_completed .todos__text{color:var(--color-6);text-decoration:line-through}.todos__check{position:relative;flex-shrink:0;width:var(--size-button);height:var(--size-button);border-radius:50%;border:2px solid var(--color-1);cursor:pointer;transition:var(--transition)}.todos__check:before{content:"âœ“";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-size:22px;color:var(--color-4);transition:var(--transition);opacity:0}.todos__text{padding:0 var(--indent-text);transition:var(--transition)}.todos__footer{display:flex;justify-content:space-between;align-items:center;gap:20px;padding:4px var(--indent-item);background:#fff;border-bottom:1px solid var(--color-1);box-shadow:0px 1px 0px rgba(70,75,79,.1)}.todos__info{font-size:16px;line-height:19px}.todos__wrapper-buttons{display:flex;gap:20px}.todos__button{padding:4px 8px;font-size:16px;line-height:19px;border:1px solid rgba(0,0,0,0);transition:var(--transition)}.todos__button_active{border-color:var(--color-6)}@media(max-width: 576px){.todos{--height-item: 62px;--indent-item: 8px;--indent-text: 16px;--size-button: 26px;--size-pseudo: 6px}.todos__footer{flex-wrap:wrap;gap:4px}.todos__wrapper-buttons{justify-content:center;order:-1;width:100%}}